import {
  createStore
} from 'vuex'

export default createStore({

  state: {
    seen: false,
    d: "null",
    chars: "abcdefghijklmnopqrstuvwxyzABCDEFGHIJKLMNOPQRSTUVWXYZ",
    text: "",
    type:"",
    error:false,
    loading:false,
    start:true,
    icons:[
      'data:image/jpeg;base64,',
      'data:image/png;base64,',
      'data:image/png;base64,'
    ]
  },
  getters: {
    countLinks: state => {
      return state.links.length
    }
  },
  mutations: {
    typem:(state,m)=>{
     if(state.type==m)
      state.type=""
    else
    state.type=m;
    },
    types:(state,t)=>{
      if(state.type==t)
        state.type=""
      else
        state.type=t
    },
    spin: (state, g) => {
      state.error=false;
      state.start=false;
      state.seen = false;
      state.loading=true;
      if(g==""){
        state.text = " "
        state.text += state.chars.charAt(Math.floor(Math.random() * state.chars.length))
      }
      else{
        state.text=g;
      } 
      
      const MOV = `https://www.omdbapi.com/?t=${state.text}&apikey=a5549d08&type=${state.type}`;
      console.log(MOV)
      fetch(MOV)
        .then(response => response.json())
        .then(dataans => {
          if(dataans.Error){
            state.error=true;
            state.seen=false
          }
          else{
            setTimeout(()=>{
              state.loading=false,state.seen=true
            },1000);
            state.d = dataans
            
          }
         
        });
    },
  },
  actions: {
  },
  modules: {}
})